:root {
  --base: #ffc600;
  --spacing: 10px;
  --blur: 0px;
}

.css-img {
  padding: var(--spacing);
  background: var(--base);
  filter: blur(var(--blur) );
  margin: 0 auto;
}

.css-variables-body {
  text-align: center;
  color: #fff;
  background: #193549;
  font-size: 50px;
  font-weight: 100;
}

.controls {
  margin-bottom: 50px;
}

.controls input {
  width: 100px;
}

.hl {
  color: var(--base);
}

